.title {
    margin-bottom: 12px;
    font-weight: 500;
    color: var(--main-color);
    text-align: center;
}

.form {
    width: 100%;
    height: 44px;
    display: flex;
}

.balance {
    width: 140px;
    padding-right: 16px;
    font-weight: 700;
    background-color: transparent;
    border: none;
    color: #000000;
    border: 2px solid var(--bg-color);
    border-radius: 22px 0 0 22px;
    text-align: end;
}

.balance:focus {
    outline: none;
}

.balance:disabled {
    box-shadow: inset var(--box-shadow);
}

.line {
    width: 0;
    height: 44px;
    border: 1px solid var(--bg-color);
}

.button {
    width: 140px;
    background-color: transparent;
    color: var(--main-color);
    border: 2px solid var(--bg-color);
    border-left: none;
    border-radius: 0 22px 22px 0;
    text-transform: uppercase;
    cursor: pointer;

    transition: background-color 300ms ease, border-color 300ms ease, color 300ms ease;
}

.button:hover {
    background-color: var(--accent-color);
    border-color: var(--accent-color);
    color: var(--bg-color);
}

.button:disabled {
    background-color: transparent;
    border-color: var(--bg-color);
    color: var(--main-color);
    box-shadow: inset var(--box-shadow);
    cursor: no-drop;
}

@media screen and (min-width: 768px) {
    .container {
        display: flex;
        align-items: center;
    }

    .title {
        margin: 0 20px;
    }

    .balance {
        padding-right: 20px;
        border-radius: 22px;
        margin-right: 15px;
    }

    .button {
        border-radius: 22px;
    }
}

@media screen and (min-width: 1280px) {
    .title {
        margin-left: 400px;
    }
}
